.header--library {
    max-width: 1024px;
    height: 230px;
    margin: 0 auto;
    padding-top: 46px;
    background-color: #2f303a;
    background-image: linear-gradient(to top, $bgOverlayColor, $bgOverlayColor),
      url('../images/bg-library-mobile@1x.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    @media screen and (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: linear-gradient(to top, $bgOverlayColor, $bgOverlayColor),
        url('../images/bg-library-mobile@2x.jpg');
    }
    @media screen and(min-width: 321px) {
      background-image: linear-gradient(to top, $bgOverlayColor, $bgOverlayColor),
        url('../images/bg-library-tablet@1x.jpg');
      padding-top: 41px;
    }
    @media screen and (min-device-pixel-ratio: 2) and (min-width: 321px),
      (min-resolution: 192dpi) and (min-width: 321px),
      (min-resolution: 2dppx) and (min-width: 321px) {
      background-image: linear-gradient(to top, $bgOverlayColor, $bgOverlayColor),
        url('../images/bg-library-tablet@2x.jpg');
    }
    @media screen and(min-width: 769px) {
      background-image: linear-gradient(to top, $bgOverlayColor, $bgOverlayColor),
        url('../images/bg-library-desktop@1x.jpg');
    }
    @media screen and (min-device-pixel-ratio: 2) and (min-width: 769px),
      (min-resolution: 192dpi) and (min-width: 769px),
      (min-resolution: 2dppx) and (min-width: 769px) {
      background-image: linear-gradient(to top, $bgOverlayColor, $bgOverlayColor),
        url('../images/bg-library-desktop@2x.jpg');
    }
  }
  
  .main-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .logo {
    display: flex;
    align-items: center;
    transition: 255ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover,
    &:focus {
        transform: scale(1.1);
    }
    &__icon{
        width: 24px;
        height: 24px;
    }
  }
  .logo__item {
    display: none;
    margin-left: 10px;
    font-weight: 500;
    font-size: 14px;
    line-height: 35px;
    color: $textLightColor;
    @media screen and(min-width: 321px) {
      display: block;
    }
    @media screen and(min-width: 768px) {
      display: block;
      font-size: 30px;
    }
  }
  
  .nav {
    color: $textLightColor;
    &__list {
      margin-left: -39px;
      display: flex;
    }
    &__item {
        position: relative;
        margin-left: 39px;
        transition: 255ms cubic-bezier(0.4, 0, 0.2, 1);
        &:hover,
        &:focus {
            color: $accentColort;
        }
    }
    &__link {
      font-weight: 500;
      font-size: 12px;
      line-height: 14px;
      text-transform: uppercase;
    }
    &__link--current {
        &::after {
        content: '';
        position: absolute;
        bottom: -1px;
        left: 0;
        width: 100%;
        height: 3px;
        background: $accentColort;
      }      
    }
  }

  .library-nav {
    margin-top: 40px;
  }